
.Intro{
  display: flex;
  height: 65vmin;
  width: 100vw;
  align-items: center;
  justify-content: space-around;
  
  
}
.alinha_Intro{
  position: relative;
  width: 50vw;
  height: 100%;
  

}
.foto_perfil{
  margin-left: 20vmin;
  width: 35vmin;
  height: 35vmin;
